import App from '@/App'
// 首页
// import Index from '@/components/Index/Index'
const Index = resolve => require(['@/components/Index/Index'], resolve);
//登录
// import Login from '@/components/Login/Login'
const Login = resolve => require(['@/components/Login/Login'], resolve);
//登录 - 《网站服务协议》
// import LoginAgreement from '@/components/Login/LoginAgreement'
const LoginAgreement = resolve => require(['@/components/Login/LoginAgreement'], resolve);
// 注册成功页面
// import SuccessRegistre from '@/components/Login/SuccessRegistre'
const SuccessRegistre = resolve => require(['@/components/Login/SuccessRegistre'], resolve);
//搜索 - 通用搜索
// import Search from '@/components/Search/Search'
const Search = resolve => require(['@/components/Search/Search'], resolve);
//搜索 - 对症找药
// import Symptom from '@/components/Search/Symptom'
const Symptom = resolve => require(['@/components/Search/Symptom'], resolve);
//个人中心
// import Myinfo from '@/components/Myinfo/Myinfo'
const Myinfo = resolve => require(['@/components/Myinfo/Myinfo'], resolve);
// import Info from '@/components/Myinfo/Info'
const Info = resolve => require(['@/components/Myinfo/Info'], resolve);
// import InfoSet from '@/components/Myinfo/InfoChange'
const InfoSet = resolve => require(['@/components/Myinfo/InfoChange'], resolve);
//个人中心 - 订单
// import Order from '@/components/Order/Order'
const Order = resolve => require(['@/components/Order/Order'], resolve);
// import OrderDetail from '@/components/Order/OrderDetail'
const OrderDetail = resolve => require(['@/components/Order/OrderDetail'], resolve);
// import Express from '@/components/Order/Express'
const Express = resolve => require(['@/components/Order/Express'], resolve);
//个人中心 - 我的收藏
// import Collect from '@/components/Myinfo/Collect'
const Collect = resolve => require(['@/components/Myinfo/Collect'], resolve);
//个人中心 - 收货地址
// import Address from '@/components/Myinfo/Address'
const Address = resolve => require(['@/components/Myinfo/Address'], resolve);
//个人中心 - 编辑/新增地址
// import AddAddress from '@/components/Myinfo/Add_address'
const AddAddress = resolve => require(['@/components/Myinfo/Add_address'], resolve);
//个人中心 - 售后服务
// import SaleServer from '@/components/Myinfo/SaleServer'
const SaleServer = resolve => require(['@/components/Myinfo/SaleServer'], resolve);
//个人中心 - 需求登记
// import Register from '@/components/Myinfo/Register'
const Register = resolve => require(['@/components/Myinfo/Register'], resolve);
//个人中心 - 设置
// import Setting from '@/components/Myinfo/Setting'
const Setting = resolve => require(['@/components/Myinfo/Setting'], resolve);
//处方笺
// import Prescript from '@/components/Prescript/Prescript'
const Prescript = resolve => require(['@/components/Prescript/Prescript'], resolve);
//处方笺 - 协议
// import Agreement from '@/components/Prescript/Agreement'
const Agreement = resolve => require(['@/components/Prescript/Agreement'], resolve);
//处方笺 - 处方笺记录
// import PrescriptDetail from '@/components/Prescript/Prescript_detail'
const PrescriptDetail = resolve => require(['@/components/Prescript/Prescript_detail'], resolve);
//分类
// import AllSort from '@/components/Sort/Sort'
const AllSort = resolve => require(['@/components/Sort/Sort'], resolve);
//资讯
// import Article from '@/components/Article/Article'
const Article = resolve => require(['@/components/Article/Article'], resolve);
// import ArticleDetail from '@/components/Article/Article_detail'
const ArticleDetail = resolve => require(['@/components/Article/Article_detail'], resolve);
//确认订单
// import Purchase from '@/components/Order/Purchase'
const Purchase = resolve => require(['@/components/Order/Purchase'], resolve);
//商品页
// import Product from '@/components/Product/Product'
const Product = resolve => require(['@/components/Product/Product'], resolve);
//产品详情页
// import ProductDetail from '@/components/Product/Product_detail'
const ProductDetail = resolve => require(['@/components/Product/Product_detail'], resolve);
//确认订单 - 需求登记
// import Toregister from '@/components/Product/Toregister'
const Toregister = resolve => require(['@/components/Product/Toregister'], resolve);
//联合用药详情
// import UnionDetail from '@/components/Product/Union_detail'
const UnionDetail = resolve => require(['@/components/Product/Union_detail'], resolve);
//頻道 - 慢性病
// import Manb from '@/components/Channel/Manb'
const Manb = resolve => require(['@/components/Channel/Manb'], resolve);
//頻道 - 女性频道
// import Cfemale from '@/components/Channel/c_female'
const Cfemale = resolve => require(['@/components/Channel/c_female'], resolve);
//頻道 - 男性频道
// import Cmale from '@/components/Channel/c_male'
const Cmale = resolve => require(['@/components/Channel/c_male'], resolve);
//頻道 - 罕见病频道
// import Crare from '@/components/Channel/crare'
const Crare = resolve => require(['@/components/Channel/crare'], resolve);


export default [
	// { path: '*', component: NotFoundComponent },
	{ //首页
		path: '/index', 
		name: 'index', 
		component: Index,
		meta: {  
		  pageTitle: '阿康大药房',  
		  keepAlive: true  
		},
		beforeEnter: (to, from, next) => {
			// console.log('首页',to, from, next)
			if( from.path == '/login' ){
			  to.meta.keepAlive = false;
			}else{
			  to.meta.keepAlive = true;
			}
				next();
			}
		},
	{ //路由重定向
		path: '/',
		redirect: '/index'
	},
	//// 子路由  ////
	{ // 全部订单
		path: '/order',
		component: Order,
		meta: {
		  requireAuth: true,
		  pageTitle: '全部订单',
		},
		children: [
			{ // 订单详情
			  path: 'order_detail',
			  component: OrderDetail,
			  meta: {  
			      pageTitle: '订单详情',  
			  },
			},
			{ // 查看物流
			  path: 'express',
			  component: Express,
			  meta: {  
			      pageTitle: '查看物流',
			  },
			},
		]
	},
	{ // 个人中心
		path: '/myinfo',
		component: Myinfo,
		meta: {
		  requireAuth: true,
		  pageTitle: '个人中心',
		}, 
		children: [
			{	//个人信息
	  			path: 'info',
	  			component: Info,
		        meta: {  
		          requireAuth: true,
		          pageTitle: '个人信息'
		        },
				children: [
					{
						path: 'infoset',
						component: InfoSet
					}
				]
			},
			{ // 我的收藏
				path: 'collect',
				component: Collect,
			    meta: {  
			        pageTitle: '我的收藏',  
			    }
			},
			{ // 收货地址
				path: 'address',
				component: Address,
			    meta: {  
			        pageTitle: '收货地址',  
			    }
			},
			{ // 编辑/新增地址
				path: 'add_address',
				component: AddAddress,
			    meta: {  
			        pageTitle: '编辑地址',  
			        // keepAlive: true  
			    }
			},
			{ // 售后服务
				path: 'sale_server',
				component: SaleServer,
			    meta: {  
			        pageTitle: '售后服务', 
			    }
			},
			{ // 需求登记
				path: 'register',
				component: Register,
			    meta: {
			        requireAuth: true, 
			        pageTitle: '需求登记'
			    }
			},
			{ // 设置
				path: 'setting',
				component: Setting,
			    meta: {  
			        pageTitle: '设置',  
			    }
			},
		]
	},
	{ //处方笺
		path: '/prescript',
		component: Prescript,
		meta: {
		    requireAuth: true,
		    pageTitle: '处方笺',
		}, 
		children: [
			{ // 协议
				path: 'agreement',
				component: Agreement,
			    meta: {  
			        pageTitle: '协议',  
			    }
			},
			{ //处方笺记录
				path: 'prescript_detail',
				component: PrescriptDetail,
			    meta: {
			        requireAuth: true,
			        pageTitle: '处方笺记录',
			    }
			},
		]
	},
	{ //全部分类
		path: '/sort',
		component: AllSort,
		meta: {  
		    pageTitle: '全部分类', 
		    keepAlive: true 
		}
	},
	{ //通用搜索
		path: '/search',
		component: Search,
		meta: {  
		    pageTitle: '搜索',  
		}
	},
	{ //对症找药
		path: '/symptom',
		component: Symptom,
		meta: {  
		    pageTitle: '对症找药',   
		    keepAlive: true 
		}
	},
	{ // 健康资讯
		path: '/article',
		component: Article,
		meta: {  
		    pageTitle: '健康咨询',  
		},
		children: [
			{
				path: 'detail' ,
				component: ArticleDetail,
			    meta: {  
			        pageTitle: '健康咨询详情',  
			    }
			}
		]
	},
	{ //确认订单
		path: '/purchase',
		name:'Purchase',
		component: Purchase,
		meta: {
		    requireAuth: true,
		    pageTitle: '确认订单'
		}, 
	},
	{ // 确认订单 - 需求登记
		path: '/toregister',
		component: Toregister,
		meta: {
		    requireAuth: false,
		    pageTitle: '提交需求'
		}, 
	},	
	{ //商品页
		path: '/product',
		component: Product,
		meta: {  
		    pageTitle: '搜索结果',  
		    keepAlive: false  
		},
		//children: [
		//  {
		//    path: 'product_detail' ,
		//    component: ProductDetail,
		//    meta: {  
		//        pageTitle: '产品详情页',  
		//    },
		//  }
		//]
	},
	{ // 产品详情页
		path: '/product_detail',
		component: ProductDetail,
		meta: {  
		    pageTitle: '产品详情页'   
		},
		beforeEnter: (to, from, next) => {
		  // console.log(to, from, next)
		  next();
		}
	},
	{ // 联合用药详情
		path: '/union_detail',
		component: UnionDetail,
		meta: {  
		    pageTitle: '联合用药',  
		},
	},
	{ //頻道 慢病频道
		path: '/manb',
		component: Manb,
		meta: {  
		    pageTitle: '慢病频道',   
		},
	},
	{ //频道 女性频道
		path: '/c_female',
		component: Cfemale,
		meta: {  
		    pageTitle: '女性频道',  
		},
	},
	{ //频道 男性频道
		path: '/c_male',
		component: Cmale,
		meta: {  
		    pageTitle: '男性频道', 
		}
	},
	{ //频道 罕见病
		path: '/crare',
		component: Crare,
		meta: {  
		    pageTitle: '罕见病频道', 
		}
	},
	{ //登录
		path: '/login',
		component: Login,
		meta: {  
		  pageTitle: '登录',   
		}
	},
	{ //完成注册页面
		path: '/success_registre',
		component: SuccessRegistre,
		meta: {  
		    pageTitle: '完成注册', 
		}
	},
	{ //《网站服务协议》
		path: '/login_agreement',
		component: LoginAgreement,
		meta: {  
		    pageTitle: '网站服务协议',  
		}
	},
	//// 子路由 end  ////
];


