// import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'

import 'lib-flexible/flexible.js'
import { Lazyload } from 'mint-ui'
import { InfiniteScroll } from 'mint-ui';
import 'mint-ui/lib/style.css'

import store from './store/store.js' //全局中引入vuex
import util from './util'
import components from './components/Public/'

Vue.config.productionTip = false;
Vue.config.debug = true;
Vue.config.devtools = false;
Vue.config.silent = true; //取消Vue所有的日志与警告

Vue.use(util);
Vue.use(Lazyload, {
  preLoad: 1.3,
  error: './static/images/ak.png', // 加载失败显示图
  loading: './static/images/ak.png',// 加载显示图
  attempt: 1
});
Vue.use(InfiniteScroll);

Object.keys( components ).forEach( (key) => {
	var name = key.replace(/(\w)/, (v) => v.toUpperCase()) // 首字母大写

	Vue.component(`com${name}`, components[key]);
} );


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



