import Vue from 'vue';
import store from '@/store/store.js' //全局中引入vuex
import Router from 'vue-router';
import routers from './routers';
import { MessageBox } from 'mint-ui';

Vue.use(Router);

const router = new 	Router({
	routes:  routers,
	mode: 'history',
	base: __dirname,
	//base: '/wap/',
	//回到顶部
	scrollBehavior (to, from, savedPosition) {
	  if (savedPosition) {
	    return savedPosition;// 返回滚动条记录的地方
	  } else {
	    return { x: 0, y: 0 }
	  }
	},
});

//路由拦截
router.beforeEach((to, from, next) => {

	// console.log(to, from, next)
    if (to.meta.requireAuth) {  // 判断该路由是否需要登录权限
        if (store.state.memberId) {  // 通过vuex state获取当前的token是否存在
            next();
        }
        else {
        	MessageBox.confirm('请先登录').then(action => {
	            next({
	                path: '/login',
	                query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
	            })
			}).catch( cancel => {
				console.log(cancel)
			} );
        }
    }
    else {
        next();
    }
	//标题
	if(to.meta.pageTitle){
		document.title = to.meta.pageTitle;
	}
});
//后置钩子
router.afterEach((to, from) => {
	//console.log('后置钩子: ', to);
	gtag('config', 'UA-108545486-2', {
	  'page_title' : to.meta.pageTitle,
	  'page_path': to.path
	});
	gtag('config', 'UA-123344424-2', {
	  'page_title' : to.meta.pageTitle,
	  'page_path': to.path
	});
});

export default router;


