/* 仓库文件 */
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state = { //一般通过外部文件引入的，东西很多的。
  //定义数据 --- 类似data
  // num: 12,  //生成了一个静态的常量
  token: sessionStorage.seslotoken,
  userPhone: sessionStorage.seslouserph,
  // memberId: 10299,
  // memberId: sessionStorage.seslomemberId,
  memberId: sessionStorage.seslomemberInfo?JSON.parse( sessionStorage.seslomemberInfo ).memberID:'',
  memberInfo: sessionStorage.seslomemberInfo?JSON.parse( sessionStorage.seslomemberInfo ):'',//会员信息
  buyProductInfo: sessionStorage.addBuyProductInfo?JSON.parse( sessionStorage.addBuyProductInfo ):'', //下单商品信息
  buyProductAdd: sessionStorage.addBbuyProductAdd?JSON.parse( sessionStorage.addBbuyProductAdd ):'',//下单商品地址信息
  edAddInfo: '',//编辑地址的信息
  orderInfo: sessionStorage.sesorderinfo?sessionStorage.sesorderinfo:'',//订单信息
}
const getters = {
    doneTodos: state => {
      // return state.num+10
    }
}
const mutations = {
  //登录
  // add (state, data){
  //   state.token = data.addToken; //token
  //   state.userPhone = data.addPhone; //用户手机
  //   state.memberId = data.addMemberID;//会员ID
  // },
  add (state, data){
    state.memberInfo = data.addMemberInfo; //会员信息
    state.memberId = data.addMemberInfo.memberID;//会员ID
    // sessionStorage.seslomemberId
    sessionStorage.setItem('seslomemberInfo', JSON.stringify( data.addMemberInfo ));
  },
  //添加购物信息
  minus (state, data){ 
    state.token = data.addToken;
    state.userPhone = data.addPhone;
  },
  //添加购物信息
  addBuyProductInfo (state, data){ 
    state.buyProductInfo = data;
  },
  //添加地址信息
  addBbuyProductAdd (state, data){ 
    state.buyProductAdd = data;
  },
  //编辑地址信息
  editAddressInfo (state, data){
    state.edAddInfo = data;
  },
  //订单信息
  saveOrderInfo (state, data){
    sessionStorage.setItem('sesorderinfo', JSON.stringify( data ));
    state.orderInfo = sessionStorage.sesorderinfo;
  }
}
export default new Vuex.Store({
  state,
  getters,
  mutations
});


