<template>
<div class="header">
	<!-- 另一种风格 -->
	<slot name="style-def">
		<mt-header :title="titles">
	      <mt-button 
	      	icon="back" 
	      	slot="left" 
	      	@click.prevent="goBack"></mt-button>
		  <mt-button 
		  	icon="more" 
		  	slot="right"
		  	@click.prevent="showMore"></mt-button>
		</mt-header>
	</slot>	
	<!-- 另一种风格 -->
    <slot name="style-two"></slot>

	<mt-popup
		class="more_block"
		v-model="isShowMore"
		position="top">
		<router-link to="/index">首页</router-link>
		<router-link to="/sort">分类</router-link>
		<router-link to="/myinfo">个人中心</router-link>
	</mt-popup>

</div>
</template>

<script>
import { Header, Button, Popup } from 'mint-ui';

export default {
	name: 'comHeaderTitle',
	data (){	
		return {
			isShowMore: false, // 是否显示更多
		}
	},	
	props: [
		'titles',
		'clearData',
	],
	components: {
		'mt-header': Header,
		'mt-button': Button,
		'mt-popup': Popup
	},
	methods: {
		// 后退
		goBack (){	
			if( this.clearData ){
				this.$emit('clearData');
			}
			this.$router.go(-1);
		},
		//显示更多
		showMore (){
			this.isShowMore = true;
		},
	}
}
</script>

<style scoped>
@import '../../../static/css/headerTitle.css';
</style>




