import Vue from 'vue'
import store from '@/store/store.js' //全局中引入vuex
import axios from 'axios'
import Qs from 'qs';
import sha256 from 'sha-256-js';
import global_ from '@/components/global/global'//引用文件

export default{
  install(Vue,options){

  	//******* webRoot  *******//
  	// 生产环境
	// Vue.prototype.webRoot = 'http://113.108.163.210:9999/AKGW-api/v1';

	// 测试环境
	Vue.prototype.webRoot = 'http://192.168.2.250:8081/AKGW-api/v1';

	// m - 生产环境
	// Vue.prototype.webRoot = 'http://47.106.157.192:8080/AKGW-api/v1';

	// 本地环境 
	// Vue.prototype.webRoot = '/apiserver'; //http://192.168.2.250:8081/AKGW-api/v1
	// Vue.prototype.webRoot = '/server';
	// Vue.prototype.webRoot = '/apihj';
	// Vue.prototype.webRoot = '/apilf';
	// Vue.prototype.webRoot = '/apiregister';
	// Vue.prototype.webRoot = webOrigin + '/AKGW-api/v1';

	// 生产环境 
	Vue.prototype.ImgSrc = 'http://113.108.163.210:9999';

	// 测试 环境
	// Vue.prototype.ImgSrc = 'http://192.168.2.250:8081';
  	
  	//******* axios  *******//
    Vue.prototype.$ajax = axios.create({
		// baseURL: 'http://113.108.163.210:9999/AKGW-api/v1',
		method: 'post',
		timeout: 1000000,
		headers: {'Content-Type': 'application/x-www-form-urlencoded'},
		transformRequest: [function (data) {
			data = Qs.stringify(data)
			return data
		}],
		withCredentials: true,
	});

	Vue.prototype.$ajax.defaults.withCredentials=true;

	// http request 拦截器
	Vue.prototype.$ajax.interceptors.request.use(
	    config => {
	        if (store.state.memberId) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
	            config.headers.Authorization = sha256(store.state.memberId + 'akjk');
	        }
	        return config;
	    },
	    err => {
	        return Promise.reject(err);
	    }
	);
	// http response 拦截器
	Vue.prototype.$ajax.interceptors.response.use(
		response => {
		    // console.log('响应： ' , response)
	      if( response.data.msg === "鉴权验证不通过！" ){
	        MessageBox.confirm('请勿非法操作').then(action => {
	          router.replace({
	              path: '/login',
	              query: {redirect: router.currentRoute.fullPath}
	          })
	        }).catch( cancel => {
	          console.log(cancel)
	        } );
	        
	      }
		    return response;
		},
		error => {
		    if (error.response) {
		        switch (error.response.status) {
		            case 401:
		                // 返回 401 清除token信息并跳转到登录页面
		                store.commit(types.LOGOUT);
		                router.replace({
		                    path: 'login',
		                    query: {redirect: router.currentRoute.fullPath}
		                })
		        }
		    }
		    return Promise.reject(error.response.data)   // 返回接口返回的错误信息
		}
	);

  	//******* 全局文件 *******//
	//全局文件
	Vue.prototype._global = global_;
  }
}


